:root {
  --yellow: #FBE8A6;
  --orange: #F4976C;
  --navy: #475074;
  --darkcyan: #B4DFE5;
  --cyan: #D2FDFF;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
  background-size: 35px 35px;
  background-image: radial-gradient(circle, rgba(0, 0, 0, 0.5) 1px, rgba(0, 0, 0, 0) 1px);
}

a {
  color: #fff;
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 1100px;
  margin: auto;
  /* overflow: hidden; */
  padding: 0 2rem;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.all-center {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

.px {
  padding: 0 0.5rem;
}
.px-1 {
  padding: 0 1rem;
}
.px-2 {
  padding: 0 2rem;
}
.px-3 {
  padding: 0 3rem;
}

.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}

.grid-5 {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1rem;
}

.contactPage,
.homePage,
.projectsPage {
  margin-bottom: 10rem;
}

/* ///////////////////////////////////////// */

@media only screen and (max-width: 900px) {
  .grid-4 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .grid-3 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .program-list-container li {
    width: 23rem !important;
  }
  
  .projects-container {
    margin: 0.5rem;
    width: 100%;
    gap: 0;
    align-items: center;
    justify-items: center;
  }
}


/* ///////////////////////////////////////// */

@media only screen and (max-width: 800px) {
  .greeting-container img {
    width: 200px;
  }

  .greeting-break {
    display: none;
  }

  .program-list-container {
    width: 1px !important;
    font-size: 1rem;
  }

  .program-list-container li {
    width: 12rem !important;
  }

}

/* ///////////////////////////////////////// */

@media only screen and (max-width: 625px) {
  .grid-3 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

/* ///////////////////////////////////////// */

@media only screen and (max-width: 550px) {
  .form-container {
    width: 400px !important;
  }
}

/* ///////////////////////////////////////// */

@media only screen and (max-width: 450px) {
  .form-container {
    width: 300px !important;
  }
}

/* ///////////////////////////////////////// */

@media only screen and (max-width: 350px) {
  .grid-4 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
  }

  .program-list-container li {
    width: 15rem !important;
    height: 2.5rem;
  }

  .form-container {
    width: 250px !important;
  }
}